import { createStore } from "vuex";

export default createStore({
  state: {
    wishes: [],
  },
  getters: {
    getWishes: (state) => state.wishes,
  },
  mutations: {
    setWishes(state, wishes: []) {
      state.wishes = wishes;
    },
  },
  actions: {
    async fetchWishes({ commit }) {
      try {
        const responce = await fetch("wishes.json");
        const wishes = await responce.json();
        commit("setWishes", wishes);
      } catch (error) {
        console.error("Ошибка загрузки JSON: ", error);
      }
    },
  },
  modules: {},
});
